<template>
  <!-- 客户管理 -->
  <div class="main_con_box">
    <div class="top_box">
      <div class="filterbox">
        <div class="elinput">
          <el-input
            style="width: 300px"
            size="mini"
            v-model="keyword"
            placeholder="编号/名称"
          ></el-input>
        </div>
        <div class="elselect">
          <el-select
            class="top_sel"
            size="mini"
            v-model="empId"
            placeholder="业务员"
          >
            <el-option
              v-for="item in emplist"
              :key="item.value1"
              :label="item.label"
              :value="item.value1"
            >
            </el-option>
          </el-select>
        </div>
        <div class="elselect">
          <el-select
            class="top_sel"
            size="mini"
            v-model="cus_statusId"
            placeholder="客户状态"
          >
            <el-option
              v-for="item in cus_status_list"
              :key="item.value2"
              :label="item.label"
              :value="item.value2"
            >
            </el-option>
          </el-select>
        </div>
        <div class="elcheckbox">
          <el-checkbox
            style="height: 30px"

            class="top_sel"
            v-model="overdue_cus"
            label="只看下次跟进逾期的客户"
          ></el-checkbox>
        </div>
        <div class="elpicker">
          <el-date-picker
            v-model="followup_date"
            size="mini"
            style="width: 300px;margin-top:10px"
            class="eldate"
            type="daterange"
            range-separator="至"
            start-placeholder="上次跟进日期"
            end-placeholder="上次跟进日期"
          >
          </el-date-picker>
        </div>
        <div>
          <el-select
          style="margin-top:10px"
            class="top_sel"
            size="mini"
            v-model="businesstype"
            placeholder="客户类型"
          >
            <el-option
              v-for="item in businesstypelist"
              :key="item.value1"
              :label="item.label"
              :value="item.value1"
            >
            </el-option>
          </el-select>
        </div>

        <div class="elbutton">
          <el-button
            class="search_btn"
            type="primary"
            size="mini"
            @click="search()"
            >查 询</el-button
          >
          <el-button  style="margin-top:10px" size="mini" @click="resetForm()">重 置</el-button>
        </div>
      </div>
      <div class="top_line2">
        <div>
          <el-button  style="margin-top:10px" type="primary" size="mini" @click="onSubmit()"
            >新 建</el-button
          >
          <el-button  style="margin-top:10px" @click="returnrouter" v-if="isouter == 1" size="mini"
            >返 回</el-button
          >
        </div>
      </div>
    </div>

    <div class="main_box">
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        :height="$store.state.innerheight - 225"
        size="mini"
        :header-cell-style="{ background: '#f5f5f5' }"
      >
        <el-table-column
          fixed
          prop="customer_number"
          label="客户编号"
          width="80"
        >
        </el-table-column>
        <el-table-column prop="customer_name" label="客户名称" width="">
        </el-table-column>
        <el-table-column prop="salesman" label="业务员" width="">
        </el-table-column>
        <el-table-column prop="customer_status" label="客户状态" width="">
        </el-table-column>
        <el-table-column prop="last_time" label="上次跟进时间" width="">
        </el-table-column>
        <el-table-column prop="next_time" label="下次跟进时间" width="">
        </el-table-column>
        <el-table-column label="操作" width="">
          <template #default="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="mini"
              >编辑</el-button
            >
            <el-button
              type="text"
              style="color: red"
              size="mini"
              @click="handleDel(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="footer_box" style="margin-top: 10px">
      <el-pagination
        v-model:currentPage="currentPage4"
        :page-sizes="[20, 50, 70, 100]"
        :page-size="20"
        layout="total, sizes, prev, pager, next, jumper"
        :total="400"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
  <div>
    <customerDelail ref="customerDelail"></customerDelail>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import { getCurrentInstance, onMounted } from "@vue/runtime-core";
import customerDelail from "../customer_management/customer_delails.vue";
export default {
  components: {
    customerDelail,
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      isouter: null,
      keyword: "",
      empId: "",
      emplist: [],
      cus_statusId: "",
      cus_status_list: [],
      overdue_cus: false,
      followup_date: "",
      tableData: [
        {
          customer_number: "1",
          customer_name: "1",
          salesman: "1",
          customer_status: "1",
          last_time: "1",
          next_time: "1",
        },
        {
          customer_number: "1",
          customer_name: "1",
          salesman: "1",
          customer_status: "1",
          last_time: "1",
          next_time: "1",
        },
        {
          customer_number: "1",
          customer_name: "1",
          salesman: "1",
          customer_status: "1",
          last_time: "1",
          next_time: "1",
        },
      ],
      currentPage4: 1,
    });
    const onSubmit = () => {
      proxy.$router.push({
        path: "/customer_edit",
        query: {
          obj: {},
          isedit: 0,
          isouter: 1,
        },
      });
    };
    const handleEdit = (res) => {
      console.log(res);
      proxy.$router.push({
        path: "/customer_edit",
        query: {
          obj: res,
          isedit: 1,
          isouter: 1,
        },
      });
    };
    const handleDel = () => {
      console.log();
    };
    const search = () => {
      console.log();
    };
    const resetForm = () => {
      console.log();
    };
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };
    const returnrouter = () => {
      proxy.$router.go(-1);
    };
    onMounted(() => {
      proxy.isouter = proxy.$route.query.isouter;
    });
    return {
      ...toRefs(state),
      onSubmit,
      handleEdit,
      handleDel,
      search,
      resetForm,
      handleSizeChange,
      handleCurrentChange,
      returnrouter,
    };
  },
};
</script>

<style scoped>
.main_con_box {
  padding: 10px;
  box-sizing: border-box;
  background-color: #fff;
}

.top_sel {
  margin-left: 10px;
}
.search_btn {
  margin-left: 10px;
}
.main_box {
  margin-top: 10px;
}
.top_box {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.filterbox {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  width: calc(100% - 210px);
}
.el-date-picker {
  margin-left: 0;
}
.eldate{
  margin-top: 10px;
}
</style>
