<template>
  <div>
    <el-dialog class="eldialog" title="新增客户" v-model="dialogVisible" width="560px">
      <div class="main_box">
        <el-form
          :label-position="labelPosition"
          :model="formLabelAlign"
          label-width="100px"
          size="mini"
        >
          <el-form-item label="客户编号" :required="true">
            <el-input v-model="formLabelAlign.number"></el-input>
          </el-form-item>
          <el-form-item label="客户名称" :required="true">
            <el-input v-model="formLabelAlign.name"></el-input>
          </el-form-item>
          <el-form-item label="业务员" prop="salesman">
            <el-select
              style="width: 100%"
              v-model="formLabelAlign.salesman"
              placeholder=""
            >
              <el-option label="张三" value=""></el-option>
              <el-option label="李四" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="联系人">
            <el-input v-model="formLabelAlign.contacts"></el-input>
          </el-form-item>
          <el-form-item label="联系电话">
            <el-input v-model="formLabelAlign.contact_number"></el-input>
          </el-form-item>
          <el-form-item label="地址">
            <el-input v-model="formLabelAlign.address"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="formLabelAlign.remarks"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" type="primary" @click="dialogVisible = false"
            >保 存</el-button
          >
          <el-button size="mini" @click="dialogVisible = false"
            >取 消</el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { reactive, toRefs } from "@vue/reactivity";
import { getCurrentInstance } from "@vue/runtime-core";

export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      dialogVisible: false,
      labelPosition: "right",
      formLabelAlign: {
        number: "",
        name: "",
        salesman: "",
        contacts: "",
        contact_number: "",
        address: "",
        remarks: "",
      },
    });
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    return {
      setshow,
      ...toRefs(state),
    };
  },
};
</script>
<style scoped>
.main_box {
  margin-top: 10px;
  width: 490px;
}
</style>
